export const portfolioReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE_URL' :
            return {...state, currentUrl: action.url}
        case 'CHANGE_SELECT_BUTTON' :
            return {...state, currentSelectButton: action.btn}

        default:
            return state
    }
}

export const ChangeUrlAc = (url) => {
    return {
        type: "CHANGE_URL", url
    }
}
export const ChangeSelectButtonAC = (btn) => {
    return {
        type: "CHANGE_SELECT_BUTTON", btn
    }
}