import React, {useEffect, useState} from 'react';
import './portfolio.scss'
import PortfolioList from "./portfolioList/portfolioList";
import {Route} from 'react-router'
import AboutWorks from "./aboutWork/aboutWork";
import {useDispatch, useSelector} from "react-redux";
import {Button} from "@material-ui/core";
import {NavLink} from "react-router-dom";
import {ChangeUrlAc} from "../../redux/portfolioReducer";

const Portfolio = () => {
    const [data, setData] = useState([])

    const select = useSelector(store => store.portfolio)

    const item = [
        {id: 'web', title: 'Web App'},
        {id: 'mobile', title: 'Mobile App'},
        {id: 'design', title: 'Design'},
    ]
    const dispatch = useDispatch()
    const liItem = item.map((i) => {
        return <PortfolioList title={i.title} id={i.id}
                              active={select.currentSelectButton === i.id}/>
    })
    console.log(select.currentSelectButton)
    useEffect(() => {
        switch (select.currentSelectButton) {
            case 'web' :
                setData(select['web'])
                break;
            case 'mobile' :
                setData(select['mobile'])
                break;
            case 'design' :
                setData(select['design'])
                break;

            default:
                setData(select['web'])

        }
    }, [select.currentSelectButton])
    return (

        <div className={'portfolio'} id={'portfolio'}>

            <h1>Portfolio</h1>
            <ul>
                {liItem}

            </ul>
            <div className="container">
                {data.map((i) => {

                    return (
                        <div className="item">
                            <img src={i.img} alt=""/>
                            <div>
                                <h3>{i.title}</h3>
                                <div>
                                    <NavLink to={`/${i.url}`} className={'btn'}
                                             onClick={() => dispatch(ChangeUrlAc(i.url))}>
                                        <Button variant="outlined" color="secondary"
                                        >View</Button>
                                    </NavLink>

                                </div>


                            </div>


                        </div>
                    )
                })}


            </div>
        </div>
    );
}

export default Portfolio;
