import React from 'react';
import './aboutWork.scss'
import {Button, Link} from "@material-ui/core";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import {ChangeUrlAc} from "../../../redux/portfolioReducer";
import {useDispatch} from "react-redux";

const AboutWorks = (props) => {
    let data;
const dispatch = useDispatch()
    props.select[props.select.currentSelectButton].map((e) => {
            if (e.url === props.select.currentUrl) {
                return data = e.work

            }

        }
    )
    console.log(data)
    return (

        data ? <div className={'aboutWork'}>
            <a onClick={() => dispatch(ChangeUrlAc(undefined))} href="#portfolio"><ArrowBackIcon className={'arrow'}/></a>
            <div className="wrapper">
                <div className="left">
                    <div className="imgBox">
                        <img src={data.img} alt=""/>
                    </div>

                </div>
                <div className="right">
                    <div className="text">
                        <h1>{data.logo}</h1>
                        <p>{data.title}</p>
                        <span>React, Redux, SASS</span>
                    </div>
                    <div className="btn">
                        <Link variant="outlined" color="primary" href={data.code} target={'_blank'}>View code</Link>
                        <Link variant="outlined" color="primary" href={data.demo} target={'_blank'}>View demo</Link>
                    </div>

                </div>
            </div>

        </div> : <div className={'wait'}>
            <h1>This page in progress...</h1>
            <p>ID page: {props.select.currentUrl}</p>
        </div>
    )


}


export default AboutWorks;
